import { Component, OnInit } from '@angular/core';
import { HttpService } from '../services/http.service';
import { environment } from 'src/environments/environment';
import { AuthService } from '../services/auth.service';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.scss']
})
export class CartComponent implements OnInit {

  public cartItems: any[] = [];
  public subscriptions: any;

  constructor(private _httpService: HttpService, private authService: AuthService) { }

  ngOnInit(): void {
    this.getSubscriptions();
    let storageItem = localStorage.getItem('cart');
    if (storageItem) {
      this.cartItems = JSON.parse(storageItem);
    } else {
      this.cartItems = [];
    }
  }

  private getSubscriptions() {
    this._httpService.getRequest(environment.SUBSCRIPTIONS, this.authService.token).subscribe((res)=>{
      let body = res.body;
      this.subscriptions = body['data'];
      console.log(this.subscriptions);
      // this.subscriptions = new Map();
    });
  }

  public getSubscriptionCost

}
